@import '../../helpers/colors';

.bar {
  z-index: 0;
}

.line {
  fill: none;
  stroke: #7888BF;
  stroke-width: 2px;
}

.dot {
  stroke: #7888BF;
  stroke-width: 2px;
}

.dayTitle {
  font-size: 1em !important;
}

.barChart {
  width: 100%;
}

.hovered-bar {
  cursor: pointer;
}

.card .footer {
  border-top: solid 1px $whiteGray;
}

.card .footer .checkbox {
  cursor: pointer;
  margin: 0 1.2em;
  font-size: 0.8em;
}

.checkbox.bold {
  font-weight: bold;
}


.axis line {
  stroke: transparent;
}

.cursor-pointer {
  cursor: pointer;
}
